<template>
    <div class="slideInUp">
        <div class="contentBox1_info_img">
            <img :src="baseUrl+'img/'+url+'.png'" alt="" srcset="">
        </div>
        <div class="contentBox1_info_text">{{cloNum}}<span>{{postfix}}</span></div>   
        <div class="contentBox1_info_text_mini">{{textMini}}</div>                 
    </div>
</template>
<script>
export default {
    name:"slideInUp",
    data() {
        return {
            numbers:0,
            timer:null,
            baseUrl:process.env.BASE_URL, 
        }
    },
    computed:{
        cloNum(){
            if(this.numbers>=1000){
                return parseInt(this.numbers/1000)+',' + (this.numbers%1000>0?this.numbers%1000:'000')
            }else{
                return this.numbers
            }
        }
    },
    props:[
        'textMini','type','number','postfix','url'
    ],
    mounted(){
        this.timer = setInterval(()=>{
            if(this.type=='clothes'){
                this.numbers+=250
            }else if(this.type=='country'){
                this.numbers+=1
            }else if(this.type=='product'){
                this.numbers+=1
            }
            if(this.numbers==this.number){
                clearInterval(this.timer)
            }
        },50)
    }
}
</script>
<style lang="less">
.slideInUp{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    .contentBox1_info_text_mini{
        color: #6B6B6B;
        font-size: 0.65rem;
    }
    .contentBox1_info_img{
        min-height: 100px;
    }
    .contentBox1_info_text{
        margin-top: 2.5rem;
        font-size: 1.5rem;
        color: #017CA4;
        font-family: 'regular';
        span{
            font-size: 0.9rem;
            color: #6B6B6B;
            margin-left: 0.5rem;
        }
    }
}

</style>

