<template>
  <div class="home">
      <banner></banner>
      <div class="contentBox1">
          <p class="contentBox1_mainTitle">誉诚防护</p>
          <p class="contentBox1_mainText">誉诚是一家致力于生产各类无纺布防护用品的公司，坚持诚信为本，质量第一。</p>
          <el-row :gutter="10">
            <el-col :xs="24" :sm="24" :md="{span:6,offset:3}">
              <transition name="fade">
                <slideInUp v-if="slideInUp1" :textMini="$t('textMini1')" type="clothes" number="15000" postfix="件" url="contentBox1_info_img1"></slideInUp>          
              </transition>  
            </el-col>
            <el-col :xs="24" :sm="24" :md="6">
              <transition name="fade">
                <slideInUp v-if="slideInUp2" :textMini="$t('textMini2')"  type="country" number="30" postfix="余个" url="contentBox1_info_img2"></slideInUp>          
              </transition>       
            </el-col>
            <el-col :xs="24" :sm="24" :md="6">
              <transition name="fade">
                <slideInUp v-if="slideInUp3" :textMini="$t('textMini3')"  type="product" number="50" postfix="余种" url="contentBox1_info_img3"></slideInUp>          
              </transition>                      
            </el-col>
          </el-row>
          <p class="contentBox1_mainTitle">誉诚产品</p>
          <div class="centerSlideBox">
            <transition name="fadeLeft">
              <div v-if="centerSlide" class="centerSlide1"></div>
            </transition>
            <transition name="fadeRight">
              <div v-if="centerSlide" class="centerSlide2"></div>
            </transition>
          </div>
          <div class="news">
            <div class="newsContent">
              <p>{{$t('NewsCenter')}}</p>
              <transition name="fade">
                <h2 v-if="newsContentText">保质保量，为客户提供全身心的呵护</h2>
              </transition>    
            </div>
          </div>
          <div class="foot">

          </div>
      </div>
  </div>
</template>

<script>
import banner from "@/components/banner.vue";
import slideInUp from "@/components/slideInUp.vue";
import { constants } from 'zlib';
import { setTimeout } from 'timers';

export default {
  name: "Home",
  data() {
    return {
      timer:null,
      slideInUp1:false,
      slideInUp2:false,
      slideInUp3:false,
      centerSlide:false,
      newsContentText:false
    }
  },
  components: {
    banner,slideInUp
  },
  methods:{
    listenScroll(){
        let scroll = document.documentElement.scrollTop
        if(scroll >=50 &&(!this.slideInUp1)&&(!this.slideInUp2)&&(!this.slideInUp3)){
          this.slideInUp1 = true
          setTimeout(()=>{
            this.slideInUp2 = true
          },200)
          setTimeout(()=>{
            this.slideInUp3 = true
          },400)
        }
        if(scroll >=600 &&(!this.centerSlide)){
          this.centerSlide = true
        }
        if(scroll >=1600 && !this.newsContentText){
          this.newsContentText = true
        }
    }
  },
  mounted(){
    this.timer = setInterval(this.listenScroll,500)
  },
  destroyed(){
    clearInterval(this.timer)
  }
};
</script>
